
<template>
  <div>
    <dragTable
      v-if="showTable"
      @reloadTable="reloadTable"
      :tableData="tableData"
      :col.sync="col"
      border
      row-key="id"
      align="left"
      @sort-change="sortchange"
      @filter-change="filterChange"
    >
      <template slot="dateHeader">
        <span></span>
      </template>
      <template slot="date" slot-scope="scope">
        {{ ": " + scope.data.date }}
      </template>

      <template slot="oprationHeader">
        <span></span>
      </template>
      <template slot="opration" slot-scope="scope">
        <el-button size="mini" @click="showId(scope.data.id)">{{
          "alert" + scope.data.id
        }}</el-button>
      </template>
    </dragTable>
  </div>
</template>

<script>
import dragTable from "@/common/dragTable/dragTable";

export default {
  name: "systemModel",

  data() {
    return {
      showTable: true,
      tableData: [
        {
          id: "1",
          date: "2016-05-02",
          name: "1",
          address: " 100 ",
        },
        {
          id: "2",
          date: "2016-05-04",
          name: "2",
          address: " 200 ",
        },
        {
          id: "3",
          date: "2016-05-01",
          name: "3",
          address: " 300 ",
        },
        {
          id: "4",
          date: "2016-05-03",
          name: "4",
          address: " 400 ",
        },
      ],

      col: [
        {
          label: "",
          prop: "date",
          isSlot: true,
          isSlotHeader: true,
        },
        {
          label: "",
          prop: "name",
          columnKey: "name",
          filters: [
            { text: "11", value: "1" },
            { text: "22", value: "2" },
            { text: "33", value: "3" },
            { text: "44", value: "4" }
          ],
          filterMethod: function(value, row, column){
            //
            const property = column["property"];
            console.log(value, row, column, 'filterMethod');
          },
        },
        {
          label: "",
          prop: "address",
          sortable: true
        },
        {
          label: "",
          prop: "opration",
          isSlot: true,
          isSlotHeader: true,
        },
      ],
    };
  },

  components: {
    dragTable,
  },

  methods: {
    sortchange({ column, prop, order }){
      console.log({ column, prop, order })
    },
    filterChange(filter){
      console.log(filter)
    },
    showId(id) {
      alert(id);
    },

    reloadTable() {
      this.showTable = false;
      this.$nextTick((_) => {
        this.showTable = true;
      });
    },
  },
};
</script>

<style>
</style>


